import { LoadDashBoardAction } from './../../message/message-action';
import { IPickupVM } from '../../model/IPickupVM';
import { AppState } from './../../message/message-state';
import { LoadingService } from './../../services/loading.service';
import { ParseService } from './../../services/parse.service';
import { UserService } from './../../services/user.service';
import { IUser } from './../../model/IUser';
import { Component, OnInit, ViewContainerRef } from '@angular/core';
import { Observable } from 'rxjs/Rx';
import { ActivatedRoute } from '@angular/router';
import { KlassService } from '../../services/klass.service';
import { MatDialog, MatDialogRef } from '@angular/material';
import { StudentPickupRequestWidgetComponent } from '../student-pickup-request-widget/student-pickup-request-widget.component';
import { Store } from '@ngrx/store';
import * as ProfileActions from '../store/profile.actions';
import * as moment from 'moment';
import { ToastsManager } from 'ng2-toastr';

@Component({
  selector: 'sp-other-profile',
  templateUrl: './other-profile.component.html',
  styleUrls: ['./other-profile.component.css']
})
export class OtherProfileComponent implements OnInit {

  user: IUser;

  student$: Observable<IUser>;
  parent$: Observable<IUser>;
  teacher$: Observable<IUser>;
  admin$: Observable<IUser>;
  user$: Observable<IUser>;

  mine$: Observable<boolean>;
  isChild$: Observable<boolean>;
  isStudent$: Observable<boolean>;
  isParent$: Observable<boolean>;
  inClass$: Observable<boolean>;


  currentUser: IUser = this.us.getUserVM(this.ps.currentUser);

  today = moment();

  constructor(
    private us: UserService,
    private ks: KlassService,
    private ps: ParseService,
    private ls: LoadingService,
    private toastr: ToastsManager,
    vRef: ViewContainerRef,
    private store: Store<AppState>,
    private route: ActivatedRoute,
    public dialog: MatDialog) { this.toastr.setRootViewContainerRef(vRef); }

  ngOnInit() {
    // this.store.dispatch(new LoadDashBoardAction());
    this.today.set({ hour: 14, minute: 0, second: 0, millisecond: 0 });
    // console.log(this.today.toDate());
    this.store.select(state => state.profile.pickupState.dirty)
      .filter(dirty => dirty !== undefined)
      .subscribe(
      dirty => {
        this.ls.setLoading(dirty);
        if (!dirty) {
          this.toastr.success('Request Sent');
        }
      }
      );
    this.route.params.subscribe(
      params => {
        this.init(params.id);
      }
    );
  }

  doPickup() {
    let dialogRef: MatDialogRef<StudentPickupRequestWidgetComponent>;
    dialogRef = this.dialog.open(StudentPickupRequestWidgetComponent, {
      height: '400px',
      width: '600px',
      data: this.user
    });
    dialogRef.afterClosed()
      .filter(data => data !== undefined)
      .subscribe(
      (pk) => {
        const user: any = pk['user'];
        const pickup: IPickupVM = <IPickupVM>{
          objectId: undefined,
          createdAt: undefined,
          updatedAt: undefined,
          parent: this.currentUser,
          student: this.user,
          done: false,
          pickUpOn: this.today.toDate(),
          firstName: user.firstName,
          lastName: user.lastName,
          ident: undefined
        };
        this.store.dispatch(new ProfileActions.AddNewPickup({
          pickup: pickup,
          file: pk.img
        }));
      },
      err => {

      }
      );
  }

  init(id: string) {
    console.log(id);
    this.student$ = this.store.map(state => state.dataState.users.students[id]);
    this.parent$ = this.store.map(state => state.dataState.users.parents[id]);
    this.teacher$ = this.store.map(state => state.dataState.users.teachers[id]);
    this.admin$ = this.store.map(state => state.dataState.users.admins[id]);

    this.user$ =
      Observable.merge(this.student$, this.parent$, this.teacher$, this.admin$)
        .filter(user => user !== undefined);

    this.mine$ = this.user$.map(user => user.objectId === this.ps.currentUser.id);

    this.isChild$ = this.user$.flatMap(user => this.us.getUserProfile(user.objectId))
      .map((profile: any) => {
        if (!profile) {
          return false;
        }
        const parent = profile.get('parent');
        return parent.id === this.ps.currentUser.id;
      });

    this.isStudent$ = this.user$.map(user => {
      return user.role === 'user' && ((this.currentUser.role === 'admin' || this.currentUser.role === 'teacher'));
    });

    this.isParent$ = this.user$.map(user => {
      return user.role === 'parent' && ((this.currentUser.role === 'admin' || this.currentUser.role === 'teacher'));
    });

    this.inClass$ = this.user$.filter(user => user.role === 'user').flatMap(user => this.ks.studentInClass(user.objectId));
  }

}



