import { IPickupVM } from './../../model/IPickupVM';
import { IUser } from './../../model/IUser';
import { Component, OnInit, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';
import { Observable } from 'rxjs/Observable';
import { getSrc } from '../../md5';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';

@Component({
  selector: 'sp-spr-widget',
  templateUrl: './student-pickup-request-widget.component.html',
  styleUrls: ['./student-pickup-request-widget.component.css']
})
export class StudentPickupRequestWidgetComponent implements OnInit {

  url = getSrc('defalut@schoolpop.ng') + '?s=256';
  pickup: IPickupVM;

  pickerForm: FormGroup;
  img:File;

  constructor(
    fb: FormBuilder,
    @Inject(MAT_DIALOG_DATA) public data: IUser,
    public dialogRef: MatDialogRef<StudentPickupRequestWidgetComponent>) {

      this.pickerForm = fb.group({
        firstName: ['', Validators.required],
        lastName: ['', Validators.required]
      });
    }

  ngOnInit() {
    this.url = this.data.avatar + '?s=256';
  }

  readUrl(event) {
    if (event.target.files && event.target.files[0]) {
      const reader = new FileReader();

      reader.onload = (e: any) => {
        this.url = e.target.result;
      };
      reader.readAsDataURL(event.target.files[0]);
      this.img = event.target.files[0];
    }
  }

  cancel() {
    // this.dialogRef.close();
  }

  onSubmit() {
    this.dialogRef.close({
      user: this.pickerForm.value,
      img: this.img
    });
  }

}



