import { SetCurrentSelectedUser, SetCurrentSelectedUserResults } from './../store/profile.actions';
import { ISchool } from './../../model/ISchool';
import { AppState } from './../../message/message-state';
import { PickupService } from './../../services/pickup.service';
import { IPickupVM } from './../../model/IPickupVM';
import { UserService } from './../../services/user.service';
import { IUser } from './../../model/IUser';
import { ParseService } from './../../services/parse.service';
import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import * as _ from 'lodash';
import { Store } from '@ngrx/store';
import * as ProfileActions from '../store/profile.actions';
import { Router } from '@angular/router';

@Component({
  selector: 'sp-my-profile',
  templateUrl: './my-profile.component.html',
  styleUrls: ['./my-profile.component.css']
})
export class MyProfileComponent implements OnInit {

  user: IUser = this.us.getUserVM(this.ps.currentUser);
  school: ISchool;

  child$: Observable<IUser> =
    this.store.select(state => state.profile.currentSelectedUser);

  children$: Observable<IUser[]> = this.store.select(state => _.values(state.dataState.users.students));

  pickups$: Observable<IPickupVM[]> = this.store.select(state => _.values(state.profile.pickupState.pickups));

  isParent = this.user.role === 'parent';
  pickups: IPickupVM[] = [];

  constructor(
    private ps: ParseService,
    private us: UserService,
    private pus: PickupService,
    private store: Store<AppState>,
    private router: Router) { }

  ngOnInit() {
    let school: ISchool;

    if (this.isParent) {
      school = undefined;
    } else {
      school = this.school;
    }

    if (this.user.role !== 'user') {
      this.store.dispatch(
        new ProfileActions.GetPickupAction({
          parent: this.user,
          school: school
        }
      ));
    } else {
      this.router.navigate(['/user', this.user.objectId]);
    }
  }

  setChild(user: IUser) {
    this.store.dispatch(new SetCurrentSelectedUser(user));
    this.store.dispatch(new SetCurrentSelectedUserResults(user));
  }

}



