import { ProfileEffectService } from './store/profile-effect.service';
import { ChildrenOverviewComponent } from './widgets/parent/child-overview.component';
import { LayoutModule } from '../commons/layout/layout.module';
import { ProfileRoute } from './profile.routes.module';
import { CustomOption } from './../toastr.options';
import { WidgetModule } from './../commons/widget/widget.module';
import { PickupService } from './../services/pickup.service';
import { KlassService } from '../services/klass.service';
import { UserService } from './../services/user.service';
import { LoadingService } from './../services/loading.service';
import { RouteService } from './../services/route.service';
import { ParseService } from './../services/parse.service';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ProfileComponent } from './profile.component';
import { MyProfileComponent } from './my-profile/my-profile.component';
import { OtherProfileComponent } from './other-profile/other-profile.component';
import { RouterModule } from '@angular/router';
import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { ProfileHeaderComponent } from './profile-header/profile-header.component';
import { StudentPickupRequestWidgetComponent } from './student-pickup-request-widget/student-pickup-request-widget.component';
import {
  StudentPickupRequestReviewWidgetComponent
} from './student-pickup-request-review-widget/student-pickup-request-review-widget.component';
import { MatTooltipModule, MatInputModule, MatButtonModule, MatCardModule } from '@angular/material';
import { ReactiveFormsModule } from '@angular/forms';
import { ToastModule, ToastOptions } from 'ng2-toastr';
import { EffectsModule } from '@ngrx/effects';

@NgModule({
  imports: [
    CommonModule,
    LayoutModule,
    MatButtonModule,
    MatCardModule,
    MatInputModule,
    MatTooltipModule,
    ProfileRoute,
    ReactiveFormsModule,
    RouterModule,
    EffectsModule.run(ProfileEffectService),
    ToastModule.forRoot(),
    WidgetModule
  ],
  declarations: [
    ChildrenOverviewComponent,
    ProfileComponent,
    MyProfileComponent,
    OtherProfileComponent,
    ProfileHeaderComponent,
    StudentPickupRequestWidgetComponent,
    StudentPickupRequestReviewWidgetComponent],
  providers: [],
  entryComponents: [
    StudentPickupRequestWidgetComponent
  ]
})
export class ProfileModule { }



