import { IPickupVM } from './../../model/IPickupVM';
import { ParseService } from '../../services/parse.service';
import { ADD_NEW_PICKUP, NewPickupAdded, GET_PICKUP_ACTION, AddAllPickups } from './profile.actions';
import { Injectable } from '@angular/core';
import { Actions, Effect } from '@ngrx/effects';
import { PickupService } from '../../services/pickup.service';
import * as ProfileActions from './profile.actions';

@Injectable()
export class ProfileEffectService {

  pickup: IPickupVM;
  file: any;

  @Effect() addPickup$ = this.actions$
    .ofType(ADD_NEW_PICKUP)
    .debug()
    .switchMap((action: ProfileActions.AddNewPickup) => {
      this.pickup = action.payload.pickup;
      return this.parse.upload(action.payload.file);
    })
    .flatMap(file => {
      this.file = file;
      return this.ps.newPickup(this.pickup, file).debug();
    })
    .map(p => {
      const pickup: IPickupVM = <IPickupVM> {
        objectId: p.id,
        createdAt: p.createdAt,
        updatedAt: undefined,
        parent: this.pickup.parent,
        student: this.pickup.student,
        done: this.pickup.done,
        pickUpOn: this.pickup.pickUpOn,
        firstName: this.pickup.firstName,
        lastName: this.pickup.lastName,
        ident: this.file.url()
      };
      return pickup;
    })
    .map(p => new NewPickupAdded(p))
    .debug();

    @Effect() getPickup$ = this.actions$
      .ofType(GET_PICKUP_ACTION)
      .switchMap(action => this.ps.getPickups(action.payload.parent))
      .map(p => new AddAllPickups(p));

  constructor(private actions$: Actions, private ps: PickupService, private parse: ParseService) { }

}



